import Vue from 'vue'
import router from './store/router'
import store from './store'
import { sync } from 'vuex-router-sync'
import App from './App.vue'
import init from './utils/init.js'

Vue.config.productionTip = false

const app = new Vue({
    store,
    router,
    ...init,
    ...App
})

sync(store, router)


export {
    app,
    router,
    store
}